import React from "react"
import { Link } from "gatsby"
import css from "./navigation.module.scss"
import { ArrowsAngleExpand, ArrowsAngleContract } from "@styled-icons/bootstrap"
import { Lightbulb as LightOff } from "@styled-icons/fa-regular"
import { Lightbulb as LightOn } from "@styled-icons/fa-solid"

export default class Navigation extends React.Component {
  render() {
    const { title, changeWidth, darkMode, dark, wide } = this.props
    //           <Link className={css.link} to="/tags">Tags</Link>

    return (
      <nav className={css.nav}>
        <Link className={css.title} to="/">
          {title}
        </Link>
        <div>
          <Link className={css.link} to="/about">
            About
          </Link>
          <button className={css.toggle} onClick={changeWidth}>
            {wide ? <ArrowsAngleContract /> : <ArrowsAngleExpand />}
          </button>
          <button className={css.toggle} onClick={darkMode}>
            {dark ? <LightOff /> : <LightOn />}
          </button>
        </div>
      </nav>
    )
  }
}
